@import url(https://fonts.googleapis.com/css2?family=Press+Start+2P&display=swap);
body {
  margin: 0;
  font-family: 'Press Start 2P', cursive;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
}

.main {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-evenly;
  height: 100vh;
}

.foot {
  display: flex;
  flex-direction: column;
  margin-top: 4em;
  align-items: center;
}

.main-title {
  font-size: 3em;
  color: midnightblue;
  text-align: center;
  text-shadow: 3px 3px mediumvioletred;
}

.error-alert {
  text-align: center;
}

.welcome {
  text-align: center;
}

a {
  color: midnightblue;
  text-decoration: none;
}

a:hover {
  color: powderblue;
}

@media only screen and (max-width: 500px) {
  .main {
    height: 650px;
  }

  .main-title {
    font-size: 2em;
    margin: 1em;
  }

  h1 {
    font-size: large;
  }
}

form {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: medium;
  color: aliceblue;
}

.form-wrapper {
  display: flex;
  flex-direction: column;
  padding: 3rem;
  align-items: center;
  justify-content: space-between;
  background-color: rgba(255, 255, 255, 0.15);
  -webkit-backdrop-filter: blur(5px);
          backdrop-filter: blur(5px);
  max-width: 600px;
  min-height: 300px;
  border-radius: 20px;
}

input {
  font-family: 'Press Start 2P', cursive;
  font-size: medium;
  border-top: transparent;
  border-left: transparent;
  border-right: transparent;
  background-color: transparent;
  border-color: aliceblue;
}

@media only screen and (max-width: 500px) {
  .form-wrapper {
    min-height: -webkit-fit-content;
    min-height: -moz-fit-content;
    min-height: fit-content;
    max-width: 250px;
  }
  form {
    font-size: small;
  }
  input {
    font-size: small;
    max-width: 220px;
  }
}

.button {
  font-family: 'Press Start 2P', cursive;
  font-weight: 700;
  border: 0;
  border-radius: 3em;
  position: relative;
  width: 180px;
  height: 60px;
  color: #fff;
  text-decoration: none;
  padding: 150px 20px;
  text-transform: uppercase;
  transition: 0.2s;
}
.button--primary {
  color: white;
  background-color: #1ea7fd;
}
.button--secondary {
  color: #333;
  background-color: transparent;
  box-shadow: rgba(0, 0, 0, 0.15) 0px 0px 0px 1px inset;
}
.button--pixel {
  background-color: black;
}

.button--pixel:hover {
  background: rgb(114 13 236) url(/static/media/pixel2.43e2ccbc.png);
  background-size: 200px;
  transition-delay: 1s;
  padding: 20px 15px;
  -webkit-animation: pixel 1s steps(8) forwards;
          animation: pixel 1s steps(8) forwards;
}
@-webkit-keyframes pixel {
  0% {
    background-position-y: 0px;
  }
  100% {
    background-position-y: -540px;
  }
}
@keyframes pixel {
  0% {
    background-position-y: 0px;
  }
  100% {
    background-position-y: -540px;
  }
}

.button--tiny {
  font-size: 12px;
  width: 100px;
  padding: 10px 16px;
  margin: 6px;
}

.button--small {
  font-size: 12px;
  width: 100px;
  padding: 10px 16px;
  margin: 6px;
}
.button--medium {
  font-size: 14px;
  padding: 11px 20px;
}
.button--large {
  font-size: 16px;
  padding: 12px 24px;
}

@media only screen and (max-width: 500px) {
  .button {
    width: 130px;
    height: 50px;
    margin: 6px;
  }

  .button--tiny {
    font-size: 10px;
    height: 30px;
    width: 80px;
    padding: 5px 8px;
    margin: 3px;
  }
}

.card {
  height: 160px;
  width: 120px;
  background-repeat: no-repeat;
}

@media only screen and (max-width: 500px) {
  .card {
    height: 160px;
    width: 80px;
  }
}

body,
html {
  background-image: radial-gradient(circle, rgb(85, 199, 32), rgb(0, 102, 26));
  /* height: 100%; */
}

.table-background {
  /* background-size: cover; */
  height: 100%;
}

.topbar {
  display: flex;
  justify-content: flex-end;
}

.table-container--top {
  /* border: black 1px solid; */
  height: 200px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.table-container--mid {
  /* border: black 1px solid; */
  height: 250px;
  display: flex;
  position: relative;
  justify-content: space-evenly;
}
.table-container--mid img {
  height: 100px;
  width: auto;
}
.mid--deck {
  display: flex;
  align-items: center;
}

.spacer {
  width: 300px;
}

.mid--pot {
  display: flex;
  align-items: center;
}

.table-container--bottom {
  /* border: black 1px solid; */
  height: 310px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}
.container--playerHand {
  display: flex;
  flex-direction: row;
  width: 300px;
  justify-content: space-evenly;
}

.bottombar {
  display: flex;
  justify-content: flex-start;
}
.bottombar img {
  height: 70px;
  width: auto;
}

@media only screen and (max-width: 500px) {
  .table-container--top {
    height: 200px;
  }
  .table-container--mid {
    height: 150px;
    display: flex;
    position: relative;
    flex-direction: row;
    justify-content: space-evenly;
  }

  .table-container--mid img {
    height: auto;
    width: 100px;
    margin: 1em;
  }
  .mid--deck {
    flex-direction: column;
    justify-content: center;
  }

  .container--playerHand {
    display: flex;
    flex-direction: row;
    width: 220px;
    justify-content: space-evenly;
  }
  .topbar {
    height: 50px;
  }
  h2 {
    font-size: large;
  }
}

.modal {
  display: block;
  position: fixed;
  z-index: 1;
  background-color: rgba(128, 128, 128, 0.5);
  left: 0;
  top: 0;
  width: 100vw;
  height: 100vh;
  overflow: auto;
}

.modal-content {
  background-color: #fefefe;
  position: fixed;
  left: 50%;
  top: 30%;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  margin: 15% auto;
  padding: 20px;
  border: 1px solid #888;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  border-radius: 20px;
  -webkit-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
}

.modal-buttons {
  display: flex;
  flex-direction: row;
  align-items: center;
}

@media only screen and (max-width: 500px) {
  .modal-buttons {
    display: flex;
    flex-direction: column;
  }
}

.content {
  display: flex;
  flex-direction: column;
  align-items: center;
  min-height: 250vh;
  justify-content: space-evenly;
}

.top-spacer {
  height: 2em;
}

.back {
  position: fixed;
  top: 0;
  left: 0;
  margin: 1em;
}
.content h1 {
  font-size: 3em;
  color: midnightblue;
  text-shadow: 2px 2px orange;
}

.icons img {
  margin: 1em;
  max-height: 145px;
}

.text-block {
  width: 50%;
  margin: 1em;
}

.members {
  display: flex;
  max-width: 70%;
  flex-direction: row;
  align-items: center;
  justify-content: space-evenly;
}

.social-icon {
  max-width: 40px !important;
  margin: 5px;
}

.single {
  max-width: 35%;
  margin: 1em;
  display: flex;
  flex-direction: column;
  align-items: center;
}
.single img {
  max-width: 200px;
}

.title {
  font-size: large;
  font-weight: bold;
  min-width: -webkit-fit-content;
  min-width: -moz-fit-content;
  min-width: fit-content;
  color: midnightblue;
  margin: 2.5em 2em;
}

.desc {
  font-size: small;
  height: 200px;
}

.social-icon-container {
  display: flex;
  justify-content: flex-start;
  width: 100%;
}

@media only screen and (max-width: 500px) {
  .content h1 {
    font-size: x-large;
  }
  .text-block {
    width: 70%;
    margin: 1em;
    font-size: small;
  }

  .icons {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
  }
  .icons img {
    max-width: 60%;
  }

  .members {
    flex-direction: column;
  }
  .top-spacer {
    height: 50px;
  }
}

